<template>
  <div class="date-inputs-container">
    <v-date-input
      v-model="internalStartDate"
      :min="minStartDate"
      :max="endDate"
      label="Start Date"
      density="comfortable"
      :hide-actions="true"
      prepend-icon=""
      prepend-inner-icon="$calendar"
      @update:modelValue="onStartDateChange"
      :variant="IS_MOBILE ? 'underlined' : 'outlined'"
      style="max-width: 160px;"
      hide-details="true"
    />
    <v-date-input
      v-model="internalEndDate"
      :min="startDate"
      :max="maxEndDate"
      density="comfortable"
      label="End Date"
      prepend-icon=""
      prepend-inner-icon="$calendar"
      :hide-actions="true"
      @update:modelValue="onEndDateChange"
      :variant="IS_MOBILE ? 'underlined' : 'outlined'"
      style="max-width: 160px;"
      hide-details="true"
    />
  </div>
</template>

<script>
import {IS_MOBILE} from '@/constants'

export default {
  name: "DateRangePicker",
  props: {
    startDate: {
      type: String,
      default: "", // expects a date string (e.g., "2024-01-01")
    },
    endDate: {
      type: String,
      default: "", // expects a date string (e.g., "2024-12-31")
    },
    minStartDate: {
      type: String,
      default: "",
    },
    maxEndDate: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      internalStartDate: new Date(this.startDate), // Initialize with the prop value
      internalEndDate: new Date(this.endDate), // Initialize with the prop value
    };
  },
  setup() {
    return {
      IS_MOBILE
    }
  },
  watch: {
    // Watch for changes in props to update internal state
    startDate(newStartDate) {
      this.internalStartDate = new Date(newStartDate);
    },
    endDate(newEndDate) {
      this.internalEndDate = new Date(newEndDate);
    },
  },
  methods: {
    onStartDateChange(value) {
      this.internalStartDate = value;
      this.$emit('update:startDate', value);
      this.$emit('dateRangeChanged');
    },
    onEndDateChange(value) {
      this.internalEndDate = value;
      this.$emit('update:endDate', value);
      this.$emit('dateRangeChanged');
    },
  },
  mounted() {
    console.log(this.minStartDate, this.maxEndDate);
  }
};
</script>

<style>
.date-inputs-container {
  display: flex;
  justify-content: center;
  gap: 32px; /* Optional: adds space between the inputs */
  align-items: center; /* Aligns inputs vertically */
  margin-top: 20px;
  margin-bottom: 5px;
}

@media (max-width: 768px) { 
  .date-inputs-container {
    margin-top: 15px; 
  }
}

</style>