import { createApp } from 'vue';
import App from './App.vue';
import 'leaflet/dist/leaflet.css';
import { createVuetify } from 'vuetify';
import 'vuetify/styles'; // Ensure you import the Vuetify styles
import '@mdi/font/css/materialdesignicons.css'; // Import Material Design Icons if needed

// Import Vuetify components and directives
import * as components from 'vuetify/components';
import * as directives from 'vuetify/directives';
import * as labsComponents from 'vuetify/labs/components'

// Create a Vuetify instance and register components and directives
const vuetify = createVuetify({
  theme: {defaultTheme: 'light'},
  directives,
  components: {
    ...components,
    ...labsComponents,
}
});

const app = createApp(App);

// Use the Vuetify instance
app.use(vuetify);

app.mount('#app');

