<template>
    <v-container id="mapCustomizationsWrapper">
        <v-row justify="end">
          <v-btn 
            @click="toggleCustomizations" 
            :prepend-icon="showMapCustomizations ? 'mdi-close-circle-outline' : null"
            :size="IS_MOBILE ? 'small' : undefined"
            >
            {{IS_MOBILE ? '⚙️🗺️✨' : 'Customize Map'}}
          </v-btn>
        </v-row>
        <v-row justify="end">
            <v-card v-if="showMapCustomizations">
              <TileLayerSelect v-model="localTileLayerUrl" />
              <v-color-picker
                v-model="localRouteColor"
                label="Route Color"
                hide-canvas
                hide-inputs
                show-swatches
                :modes="['hex']"
                :swatches="ROUTE_COLOR_SWATCHES"
              />
            </v-card>
        </v-row>
    </v-container>
  </template>
  
  <script>
  import TileLayerSelect from './TileLayerSelect.vue';
  import { ROUTE_COLOR_SWATCHES, IS_MOBILE } from '@/constants';
  
  export default {
    components: {
      TileLayerSelect,
    },
    props: {
      tileLayerUrl: {
        type: String,
        required: true,
      },
      routeColor: {
        type: String,
        required: true,
      },
    },
    data() {
      return {
        showMapCustomizations: false,
        localTileLayerUrl: this.tileLayerUrl, // Create local copy of tileLayerUrl
        localRouteColor: this.routeColor, // Create local copy of routeColor
      };
    },
    watch: {
      // Watch for changes in the props and update local copies
      tileLayerUrl(newVal) {
        this.localTileLayerUrl = newVal;
      },
      routeColor(newVal) {
        this.localRouteColor = newVal;
      },
      // Emit changes to the parent whenever local values change
      localTileLayerUrl(newVal) {
        this.$emit('update:tileLayerUrl', newVal);
      },
      localRouteColor(newVal) {
        this.$emit('update:routeColor', newVal);
      },
    },
    methods: {
      toggleCustomizations() {
        this.showMapCustomizations = !this.showMapCustomizations;
      },
    },
    setup() {
      return {
        ROUTE_COLOR_SWATCHES,
        IS_MOBILE
      };
    },
  };
  </script>
  
  <style>
    #mapCustomizationsWrapper {
        position: absolute;
        top: 0px;
        right: 0px;
        z-index: 1000; /* Ensure it stays above other map elements */
        margin: 10px;
    }
    
    #mapCustomizationsWrapper .v-card {
        margin-top: 10px;
    }

    .v-color-picker-swatches__color {
        border: ridge;
    }
  </style>
  