<template>
  <div></div>
</template>

<script>
import L from 'leaflet';
import 'leaflet.heat';
import {HEATMAP_SETTINGS} from '@/constants'

export default {
  name: 'HeatmapLayer',
  props: {
    mapInstance: {
      type: Object,
      required: true,
    },
    points: {
      type: Array,
      required: true,
    },
    showHeatmap: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    showHeatmap: 'toggleHeatmap',
    points() {
      if (this.showHeatmap) {
        this.updateHeatmap();
      }
    },
  },
  methods: {
    addHeatmap() {
      if (this.mapInstance && this.points.length > 0) {
        this.heatmapLayer = L.heatLayer(this.points, HEATMAP_SETTINGS).addTo(this.mapInstance);
      }
    },
    removeHeatmap() {
      if (this.mapInstance && this.heatmapLayer) {
        this.mapInstance.removeLayer(this.heatmapLayer);
        this.heatmapLayer = null;
      }
    },
    updateHeatmap() {
      this.removeHeatmap();
      this.addHeatmap();
    },
    toggleHeatmap(val) {
      if (val) {
        this.addHeatmap();
      } else {
        this.removeHeatmap();
      }
    },
  },
};
</script>
