<template>
  <div id="FullMapApp">
    <header :style="{backgroundColor: BACKGROUND_COLOR}">
      <h1>{{ daysInRange }} days of Van Life</h1>
        <text v-if="IS_MOBILE">{{ totalTurns.toLocaleString() }} turns / {{ totalDistance.toLocaleString() }} miles / {{ totalDuration.toLocaleString() }} hours of driving</text>
        <h3 v-else>{{ totalTurns.toLocaleString() }} turns over {{ totalDistance.toLocaleString() }} miles in {{ totalDuration.toLocaleString() }} hours of driving</h3>
    </header>
    <v-bottom-sheet>
      <template v-slot:activator="{ props }">
        <v-btn 
          v-bind="props" 
          icon="mdi-information-slab-circle-outline" 
          color="disabled"
          variant="plain" 
          class="info-button"
          style="position: absolute"
          :size="IS_MOBILE ? 'large' : 'x-large'"
        />
      </template>
      <v-card density="compact" tite="About">
        <v-card-item>
          <v-card-title>About this Van Life Map</v-card-title>
        </v-card-item>
        <v-card-text>
          <p>In December 2022, Emily and Ryan left the rat race in San Francisco and moved into a van. 
          For the next couple years, they parked their home-on-wheels in breathtaking spots across the country, 
          logging each location as they navigated the ups and downs of life in a 112 square foot Winnebago. </p>
          <p> While they lived the #VanLife, the rest of us were stuck in sedentary buildings, scrolling through
          <a href="https://www.instagram.com/skate_wives/" target="_blank">@skate_wives</a> to follow along.
          One of those left behind pulled this map together so that you too can dream of scenic highways, 
          creative shower solutions, and the evolution of odors that come with two years in a van.</p>
        </v-card-text>
        <v-card-actions>
          <v-btn variant="plain" href="https://travisdowns.com" target="_blank" density="compact">Created by Travis Downs</v-btn>
        </v-card-actions>
      </v-card>
    </v-bottom-sheet>
    <div id="controlsContainer" :style="{backgroundColor: '#F8F8F8'}">
      <div id="timeframe">
        <v-btn-toggle v-model="defaultTimeRange" variant="outlined" mandatory divided :density="IS_MOBILE ? 'compact' : 'comfortable'" >
          <v-btn
            v-for="TIME_RANGE in DEFAULT_TIME_RANGES"
            :key="TIME_RANGE.value"
            :value="TIME_RANGE.value"
            @click="setDateRange(TIME_RANGE)"
            :size="IS_MOBILE ? 'small' : undefined"
          >
            {{ IS_MOBILE ? TIME_RANGE.mobileLabel : TIME_RANGE.label }}
          </v-btn>
        </v-btn-toggle>
        <div v-if="dateRangeDescription" v-html="dateRangeDescription"></div>
        <DateRangePicker
          v-model:startDate="startDate"
          v-model:endDate="endDate"
          :minStartDate="MIN_DATE_STRING"
          :maxEndDate="MAX_DATE_STRING"
          @dateRangeChanged="handleManualDateRange"
        />
      </div>
  
      <div id="mapLayerControls" class="center-container">
        <v-switch :label="IS_MOBILE ? '🚐' : 'Show Van'" v-model="showVan" density="compact" color="primary" :disabled="!!intervalId" hide-details="true" />
        <v-switch :label="IS_MOBILE ? '〰️' : 'Driving Route'" v-model="showRoutePolylines" density="compact" color="primary" hide-details="true" />
        <v-switch :label="IS_MOBILE ? '💤' : 'Sleep Spots'" v-model="showHeatmap" density="compact" color="primary" hide-details="true" />
        <v-switch :label="IS_MOBILE ? '📍' : 'Start and End'" v-model="showStartAndEnd" density="compact" color="primary" hide-details="true" />
      </div>
  
      <div id="vanPositionContainer" v-if="showVan">
        <v-btn density="compact" v-if="showVan" @click="toggleAutoDriving" :icon="intervalId ? 'mdi-pause' : 'mdi-play'"></v-btn>
        <v-slider
          v-if="showVan"
          v-model="vanPositionIndex"
          :min="0"
          :max="Math.max(0, vanRouteCoordinates.length - 1)"
          :step="1"
          max-width="600px"
          hide-details="true"
          thumb-label="always"
          thumb-size="13"
        >
          <template v-slot:thumb-label>
            {{ vanDateFromPosition }}
          </template>
        </v-slider>
      </div>
    </div>

    <div id="map" style="height: 80vh; flex-grow: 1;">
      <map-customizations v-model:tileLayerUrl="tileLayerUrl" v-model:routeColor="routeColor" />
      <l-map :zoom="DEFAULT_MAP_ZOOM" :center="DEFAULT_MAP_CENTER" style="height: 100%" @ready="onMapReady">
        <l-tile-layer :key="tileLayerUrl" :url="tileLayerUrl"></l-tile-layer>
        <div id="routePolylines" v-if="showRoutePolylines">
          <l-polyline v-for="(route, index) in filteredRoutes" :key="index" :lat-lngs="route.coordinates" :color="routeColor" />
        </div>
        <l-marker v-if="showStartAndEnd && startLatLng" :lat-lng="startLatLng" :icon="startIcon" />
        <l-marker v-if="showStartAndEnd && endLatLng" :lat-lng="endLatLng" :icon="endIcon" />
        <l-marker v-if="showVan && vanPosition" :lat-lng="vanPosition" :icon="currentVanIcon" />
        <HeatmapLayer
          v-if="mapInstance"
          :map-instance="mapInstance"
          :points="heatmapPoints"
          :show-heatmap="showHeatmap"
        />
      </l-map>
    </div>
  </div>
</template>

<script>
import { LMap, LPolyline, LMarker, LTileLayer } from '@vue-leaflet/vue-leaflet';
import L from 'leaflet';
import DateRangePicker from './components/DateRangePicker.vue';
import HeatmapLayer from './components/HeatmapLayer.vue';
import MapCustomizations from './components/MapCustomizations.vue';
import { IS_MOBILE, DEFAULT_MAP_CENTER, DEFAULT_MAP_ZOOM, MIN_DATE_STRING, MAX_DATE_STRING, DEFAULT_TILE, DEFAULT_TIME_RANGES, CORE_COLOR, BACKGROUND_COLOR } from '@/constants';

export default {
  components: {
    LMap,
    LPolyline,
    LMarker,
    LTileLayer,
    DateRangePicker,
    HeatmapLayer,
    MapCustomizations
  },
  setup() {
    return {
      MIN_DATE_STRING,
      MAX_DATE_STRING,
      DEFAULT_MAP_CENTER,
      DEFAULT_MAP_ZOOM,
      DEFAULT_TIME_RANGES,
      CORE_COLOR, 
      BACKGROUND_COLOR,
      IS_MOBILE
    };
  },
  data() {
    return {
      startDate: MIN_DATE_STRING,
      endDate: MAX_DATE_STRING,
      dateRangeDescription: null,
      defaultTimeRange: DEFAULT_TIME_RANGES[0].value,
      routeData: {},
      mapInstance: null,
      routeColor: CORE_COLOR, 
      tileLayerUrl: DEFAULT_TILE,
      showHeatmap: false,
      showRoutePolylines: true,
      showStartAndEnd: true,
      showVan: false,
      startIcon: L.icon({
        iconUrl: 'location-pin.png',
        iconSize: [32, 32],
        iconAnchor: [16, 30]
      }),
      endIcon: L.icon({
        iconUrl: 'end-icon.png',
        iconSize: [32, 32],
        iconAnchor: [4, 32]
      }),
      vanPositionIndex: 0,
      intervalId: null,
      previousVanPositionIndex: null,
      vanIconRight: L.icon({
        iconUrl: 'camper-van-right.png',
        iconSize: [32, 32],
        iconAnchor: [26, 26]
      }),
      vanIconLeft: L.icon({
        iconUrl: 'camper-van-left.png',
        iconSize: [32, 32],
        iconAnchor: [8, 26]
      }),
      currentVanIcon: null
    };
  },
  computed: {
    filteredRoutes() {
      const start = new Date(this.startDate);
      const end = new Date(this.endDate);
      return Object.entries(this.routeData)
        .filter(([date]) => {
          const routeDate = new Date(date);
          return routeDate >= start && routeDate <= end;
        })
        .map(([, route]) => route)
        .filter(route => Array.isArray(route.coordinates));
    },
    totalDistance() {
      const meters = this.filteredRoutes.reduce((total, route) => total + (route.distance || 0), 0);
      return Math.round(meters * 0.000621371);
    },
    totalDuration() {
      const seconds = this.filteredRoutes.reduce((total, route) => total + (route.duration || 0), 0);
      return Math.round(seconds / 3600);
    },
    totalTurns() {
      return this.filteredRoutes.reduce((total, route) => total + (route.turns || 0), 0)
    },
    daysInRange() {
      return this.filteredRoutes.length;
    },
    heatmapPoints() {
      const frequencyMap = new Map();
      this.filteredRoutes.forEach(route => {
        const [lat, lng] = route.coordinates[0];
        const key = `${lat},${lng}`;
        if (frequencyMap.has(key)) {
          frequencyMap.set(key, frequencyMap.get(key) + 1);
        } else {
          frequencyMap.set(key, 1);
        }
      });
      const maxCount = Math.max(...frequencyMap.values());
      return Array.from(frequencyMap).map(([key, count]) => {
        const [lat, lng] = key.split(',').map(Number);
        const normalizedCount = count / maxCount;
        return [lat, lng, normalizedCount];
      });
    },
    startLatLng() {
      if (this.filteredRoutes.length > 0) {
        return this.filteredRoutes[0].coordinates[0];
      }
      return null;
    },
    endLatLng() {
      if (this.filteredRoutes.length > 0) {
        const lastRoute = this.filteredRoutes[this.filteredRoutes.length - 1];
        return lastRoute.coordinates[lastRoute.coordinates.length - 1];
      }
      return null;
    },
    vanRouteCoordinates() {
      return this.filteredRoutes.flatMap(route => route.coordinates);
    },
    vanPosition() {
      if (this.vanRouteCoordinates.length > 0 && this.vanPositionIndex < this.vanRouteCoordinates.length) {
        return this.vanRouteCoordinates[this.vanPositionIndex];
      }
      return null;
    },
    vanDateFromPosition() {
      const routes = this.filteredRoutes;
      let positionIndex = this.vanPositionIndex;
      // check each route
      for (let i = 0; i < routes.length; i++) {
        console.log(i);
        const route = routes[i];
        const coordinatesCount = route.coordinates.length;
        // If the current position is within this route
        if (positionIndex < coordinatesCount) {
          return route.date;
        }
        // Subtract the number of coordinates in this route from the position index
        positionIndex -= coordinatesCount;
      }
      return null;
    }
  },
  watch: {
    startDate: 'processDateRange',
    endDate: 'processDateRange',
    vanPositionIndex: 'updateVanIcon',
    showVan(newVal) {
      if (!newVal) {
        this.stopAutoDriving();
      }
    },
    vanRouteCoordinates(newCoords) {
      if (newCoords.length === 0) {
        this.vanPositionIndex = 0;
      } else if (this.vanPositionIndex >= newCoords.length) {
        this.vanPositionIndex = newCoords.length - 1;
      }
    }
  },
  methods: {
    setDateRange(val) {
      this.startDate = val.startDate;
      this.endDate = val.endDate;
      this.handleAutoDateRange(val);
    },
    processDateRange() {
      this.vanPositionIndex = 0;
    },
    handleAutoDateRange(val) {
      this.dateRangeDescription = IS_MOBILE ? val.mobileDescription : val.description;
    },
    handleManualDateRange() {
      this.dateRangeDescription = null;
      this.defaultTimeRange = null;
    },
    async loadRouteData() {
      try {
        const response = await fetch('journey.json');
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        this.routeData = data;
      } catch (error) {
        console.error("Failed to load route data:", error);
      }
    },
    onMapReady(map) {
      this.mapInstance = map;
    },
    updateVanIcon() {
      if (this.previousVanPositionIndex !== null) {
        const oldPosition = this.vanRouteCoordinates[this.previousVanPositionIndex];
        const newPosition = this.vanRouteCoordinates[this.vanPositionIndex];
        if (newPosition && oldPosition) {
          if (newPosition[1] > oldPosition[1]) {
            this.currentVanIcon = this.vanIconRight;
          } else {
            this.currentVanIcon = this.vanIconLeft;
          }
        }
      } else {
        this.currentVanIcon = this.vanIconRight;
      }
      this.previousVanPositionIndex = this.vanPositionIndex;
    },
    toggleAutoDriving() {
      if (this.intervalId === null) this.startAutoDriving();
      else this.stopAutoDriving();
    },
    startAutoDriving() {
      if (this.intervalId === null) {
        this.intervalId = setInterval(() => {
          this.vanPositionIndex = Math.min(this.vanPositionIndex + 25, this.vanRouteCoordinates.length - 1);
          if (!this.showVan) this.stopAutoDriving();
          if (this.vanPositionIndex >= this.vanRouteCoordinates.length) {
            this.stopAutoDriving();
          }
        }, 10);
      }
    },
    stopAutoDriving() {
      console.log(this.vanPositionIndex, this.vanRouteCoordinates.length);
      if (this.vanPositionIndex >= this.vanRouteCoordinates.length - 1) this.vanPositionIndex = 0;
      clearInterval(this.intervalId);
      this.intervalId = null;
    }
  },
  mounted() {
    this.loadRouteData();
    this.processDateRange();
    this.currentVanIcon = this.vanIconRight;
  }
};
</script>

