<template>
  <div class="tile-selector-container">
    <v-btn @click="toggle" color="primary" variant="outlined" >🎲 Randomize Map Style</v-btn>
    <v-select
      :items="tileOptions"
      item-title="name"
      item-value="url"
      label="Select Map Style"
      v-model="selectedTile"
      density="comfortable"
      variant="outlined"
    />
  </div>
</template>
  
<script>
  import {TILE_OPTIONS, DEFAULT_TILE} from '@/constants';
  export default {
    props: {
      modelValue: {
        type: String,
        default: DEFAULT_TILE
      }
    },
    data() {
      return {
        selectedTile: DEFAULT_TILE,
        tileOptions: TILE_OPTIONS,
      };
    },  
    watch: {
      selectedTile(newValue) {
        this.$emit('update:modelValue', newValue);
      },
    },
    methods: {
      toggle () {
        const randomIndex = Math.floor(Math.random() * this.tileOptions.length);
        this.selectedTile = this.tileOptions[randomIndex].url;
      }
    },
  };
</script>

<style scoped>
  .tile-selector-container {
    margin: 10px 10px 0px 10px;
  }

  .v-btn {
    margin: 0 0 20px;
  }
</style>